import React from 'react';
import PageTransition from '../components/Common/PageTransition';

const BuyingGuidesPage = () => {
    return (
        <PageTransition>
            <div className="max-w-4xl mx-auto py-10 px-4 text-text-light pt-24">
                <h1 className="text-4xl font-extrabold text-brand-accent mb-6">Buyer's Guides</h1>
                <div className="bg-bg-dark-card p-6 rounded-xl shadow-lg border border-gray-700 space-y-6">
                    <div>
                        <h2 className="text-2xl font-bold text-brand-primary mb-2">How to Buy</h2>
                        <p className="text-text-muted">1. Finalize Your Budget (Use our EMI Calculator!)<br/>2. Shortlist Locations<br/>3. Verify Legal Documents...</p>
                    </div>
                    <div>
                        <h2 className="text-2xl font-bold text-brand-primary mb-2">When to Buy</h2>
                        <p className="text-text-muted">Look for periods of low interest rates and check seasonal developer offers...</p>
                    </div>
                </div>
            </div>
        </PageTransition>
    );
};

export default BuyingGuidesPage;