import React from 'react';
import PageTransition from '../components/Common/PageTransition';

const FAQPage = () => {
    return (
        <PageTransition>
            <div className="max-w-4xl mx-auto py-10 px-4 text-text-light pt-24">
                <h1 className="text-4xl font-extrabold text-brand-accent mb-6">Frequently Asked Questions (FAQ)</h1>
                <div className="bg-bg-dark-card p-6 rounded-xl shadow-lg border border-gray-700 space-y-6">
                    <div>
                        <h2 className="text-lg font-bold text-brand-primary">1. How accurate is the price prediction?</h2>
                        <p className="text-text-muted text-sm">Our AI model (XGBoost) is trained on historical data from your selected dataset. It provides a highly accurate estimate based on Location, BHK, and Area.</p>
                    </div>
                    <div>
                        <h2 className="text-lg font-bold text-brand-primary">2. How does the Chatbot work?</h2>
                        <p className="text-text-muted text-sm">The chatbot uses Google's Gemini API. It is fed context about our ML model and can answer general real estate questions.</p>
                    </div>
                </div>
            </div>
        </PageTransition>
    );
};

export default FAQPage;