import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import PageTransition from '../components/Common/PageTransition';

// Conversion factors (relative to 1 Sq Ft)
const CONVERSIONS = {
    sqft: 1,
    sqmeter: 0.092903,
    sqyard: 0.111111,
    acre: 0.0000229568,
    bigha: 0.000165289, // Varies by region, using a standard
};

const AreaConverterPage = () => {
    const [values, setValues] = useState({
        sqft: 1000,
        sqmeter: 92.90,
        sqyard: 111.11,
        acre: 0.023,
    });

    const handleConversion = (e) => {
        const { name, value } = e.target;
        const inputVal = parseFloat(value) || 0;
        
        if (name === 'sqft') {
            setValues({
                sqft: inputVal,
                sqmeter: (inputVal * CONVERSIONS.sqmeter).toFixed(2),
                sqyard: (inputVal * CONVERSIONS.sqyard).toFixed(2),
                acre: (inputVal * CONVERSIONS.acre).toFixed(4),
            });
        } else if (name === 'sqmeter') {
            const baseSqFt = inputVal / CONVERSIONS.sqmeter;
            setValues({
                sqft: baseSqFt.toFixed(2),
                sqmeter: inputVal,
                sqyard: (baseSqFt * CONVERSIONS.sqyard).toFixed(2),
                acre: (baseSqFt * CONVERSIONS.acre).toFixed(4),
            });
        }
    };

    return (
        <PageTransition>
            <div className="max-w-4xl mx-auto py-10 px-4 text-text-light pt-24">
                <motion.h1 
                    initial={{ y: -10, opacity: 0 }}
                    animate={{ y: 0, opacity: 1 }}
                    className="text-4xl font-extrabold text-brand-primary mb-8 text-center"
                >
                    Area Unit Converter
                </motion.h1>

                <motion.div 
                    initial={{ scale: 0.95, opacity: 0 }}
                    animate={{ scale: 1, opacity: 1 }}
                    className="bg-bg-dark-card p-8 rounded-xl shadow-2xl border border-gray-700"
                >
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        {/* Sq Ft */}
                        <div>
                            <label className="block text-sm font-medium text-text-muted mb-1">Square Feet (sq ft)</label>
                            <input type="number" name="sqft" value={values.sqft} onChange={handleConversion} 
                                   className="w-full p-3 bg-gray-800 rounded text-text-light placeholder-text-muted" />
                        </div>
                        {/* Sq Meter */}
                        <div>
                            <label className="block text-sm font-medium text-text-muted mb-1">Square Meter (sq m)</label>
                            <input type="number" name="sqmeter" value={values.sqmeter} onChange={handleConversion} 
                                   className="w-full p-3 bg-gray-800 rounded text-text-light placeholder-text-muted" />
                        </div>
                        {/* Sq Yard */}
                        <div>
                            <label className="block text-sm font-medium text-text-muted mb-1">Square Yard (sq yd)</label>
                            <input type="number" name="sqyard" value={values.sqyard} onChange={handleConversion} 
                                   className="w-full p-3 bg-gray-800 rounded text-text-light placeholder-text-muted" />
                        </div>
                        {/* Acre */}
                        <div>
                            <label className="block text-sm font-medium text-text-muted mb-1">Acre</label>
                            <input type="number" name="acre" value={values.acre} onChange={handleConversion} 
                                   className="w-full p-3 bg-gray-800 rounded text-text-light placeholder-text-muted" />
                        </div>
                    </div>
                </motion.div>
            </div>
        </PageTransition>
    );
};

export default AreaConverterPage;