import React from 'react';
import { Link } from 'react-router-dom';

const HomePage = () => {
    return (
        <div className="flex flex-col items-center w-full min-h-[90vh] text-text-light">
            
            {/* --- 1. Hero Section (Image Background) --- */}
            {/* Background image is loaded via Tailwind config and index.css */}
            <div className="bg-hero-house bg-center bg-cover h-[80vh] flex items-center justify-center w-full shadow-inner">
                <div className="hero-text text-center p-8 bg-bg-dark-primary/70 rounded-xl">
                    <h1 className="text-6xl font-bold text-text-light mb-5">
                        Find Your Next Dream Home
                    </h1>
                    
                    {/* Animated Search Button */}
                    <Link to="/predict">
                        <button className="bg-brand-accent text-bg-dark-primary py-4 px-8 rounded-lg font-semibold text-lg cursor-pointer 
                                           transition duration-300 hover:bg-white hover:text-bg-dark-primary transform hover:scale-105 shadow-xl">
                            Start Price Prediction
                        </button>
                    </Link>
                </div>
            </div>

            {/* --- 2. Featured Content Section --- */}
            <div className="w-full max-w-7xl mx-auto py-12 px-4 text-center">
                
                <h2 className="text-4xl font-extrabold text-text-light mb-10">
                    Featured Property Insights
                </h2>

                <div className="flex flex-wrap justify-center gap-6">
                    
                    {/* CARD 1: Modern Interior - IMAGE ADDED */}
                    <div className="bg-bg-dark-card rounded-xl overflow-hidden w-[250px] shadow-2xl transition duration-300 hover:translate-y-[-5px]">
                        <img 
                            src="https://picsum.photos/seed/interior/300/180" 
                            alt="Modern Living Room" 
                            className="w-full h-[180px] object-cover" 
                        />
                        <div className="p-4 text-left">
                            <p className="text-xl font-extrabold text-brand-accent mb-1">95 Lakhs</p>
                            <p className="text-sm font-medium text-text-muted">3 BHK, Whitefield</p>
                        </div>
                    </div>
                    
                    {/* CARD 2: Exterior House - IMAGE ADDED */}
                    <div className="bg-bg-dark-card rounded-xl overflow-hidden w-[250px] shadow-2xl transition duration-300 hover:translate-y-[-5px]">
                        <img 
                            src="https://picsum.photos/seed/house/300/180" 
                            alt="Exterior House" 
                            className="w-full h-[180px] object-cover" 
                        />
                        <div className="p-4 text-left">
                            <p className="text-xl font-extrabold text-brand-accent">1.5 Cr</p>
                            <p className="text-sm font-medium text-text-muted">4 BHK, Koramangala</p>
                        </div>
                    </div>

                    {/* CARD 3: New High-rise Project - IMAGE ADDED */}
                    <div className="bg-bg-dark-card rounded-xl overflow-hidden w-[250px] shadow-2xl transition duration-300 hover:translate-y-[-5px]">
                        <img 
                            src="https://picsum.photos/seed/building/300/180" 
                            alt="New High-rise Project" 
                            className="w-full h-[180px] object-cover" 
                        />
                        <div className="p-4 text-left">
                            <p className="text-xl font-extrabold text-brand-accent">2,300+</p>
                            <p className="text-sm font-medium text-text-muted">New Projects</p>
                        </div>
                    </div>

                    {/* CARD 4: Budget Property - IMAGE ADDED */}
                    <div className="bg-bg-dark-card rounded-xl overflow-hidden w-[250px] shadow-2xl transition duration-300 hover:translate-y-[-5px]">
                        <img 
                            src="https://picsum.photos/seed/budget/300/180" 
                            alt="Budget Property" 
                            className="w-full h-[180px] object-cover" 
                        />
                        <div className="p-4 text-left">
                            <p className="text-xl font-extrabold text-brand-accent">895 Homes</p>
                            <p className="text-sm font-medium text-text-muted">Explore Budget Options</p>
                        </div>
                    </div>
                </div>
            </div>
            
        </div>
    );
};

export default HomePage;