import React, { useState, useEffect, useCallback } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import PageTransition from '../components/Common/PageTransition';
import ListingCard from '../components/Listings/ListingCard';
// import { fetchProperties } from '../api/propertyapi'; // API import REMOVED

// --- DUMMY DATA SET (Replaces the need for backend fetch) ---
const allDummyListings = [
    { id: 101, location: 'Whitefield', bhk: 2, area: 1200, price: 80.00, image_url: 'https://picsum.photos/seed/wp1/400/250', description: 'Near IT hub' },
    { id: 102, location: 'Indiranagar', bhk: 3, area: 1500, price: 120.00, image_url: 'https://picsum.photos/seed/ip2/400/250', description: 'Posh area' },
    { id: 103, location: 'Hebbal', bhk: 4, area: 2400, price: 180.00, image_url: 'https://picsum.photos/seed/hp3/400/250', description: 'Near Airport road' },
    { id: 104, location: 'Koramangala', bhk: 2, area: 1000, price: 65.00, image_url: 'https://picsum.photos/seed/wp4/400/250', description: 'Affordable' },
    { id: 105, location: 'Whitefield', bhk: 3, area: 1600, price: 95.00, image_url: 'https://picsum.photos/seed/wp5/400/250', description: 'New Construction' },
    { id: 106, location: 'Sarjapur', bhk: 1, area: 650, price: 35.00, image_url: 'https://picsum.photos/seed/sp6/400/250', description: 'Budget Friendly' },
];
// --- END DUMMY DATA ---

const PropertiesPage = () => {
  const [properties, setProperties] = useState([]);
  const [loading, setLoading] = useState(true);
  const [filters, setFilters] = useState({
    location: '',
    min_price: '',
    max_price: '',
    bhk: '',
  });

  // --- FUNCTION TO FILTER DUMMY DATA (Client-Side) ---
  const loadProperties = useCallback((currentFilters) => {
    setLoading(true);
    
    let filtered = allDummyListings;
    
    const minPrice = parseFloat(currentFilters.min_price);
    const maxPrice = parseFloat(currentFilters.max_price);
    const bhkFilter = parseInt(currentFilters.bhk);

    if (!isNaN(minPrice)) {
        filtered = filtered.filter(p => p.price >= minPrice);
    }
    if (!isNaN(maxPrice)) {
        filtered = filtered.filter(p => p.price <= maxPrice);
    }
    if (!isNaN(bhkFilter) && bhkFilter > 0) {
        filtered = filtered.filter(p => p.bhk === bhkFilter);
    }
    if (currentFilters.location) {
        const loc = currentFilters.location.toLowerCase();
        filtered = filtered.filter(p => p.location.toLowerCase().includes(loc));
    }
    
    // Simulate API delay
    setTimeout(() => {
        setProperties(filtered);
        setLoading(false);
    }, 300);
  }, []); 

  useEffect(() => {
    // Load properties initially (with empty filters)
    loadProperties(filters); 
  }, [filters, loadProperties]); 

  const handleFilterChange = (e) => {
    const { name, value } = e.target;
    setFilters(prevFilters => ({ ...prevFilters, [name]: value }));
  };
  
  const handleSearch = (e) => {
      e.preventDefault();
      loadProperties(filters); // Re-run the filter function
  };

  const handleClear = () => {
      setFilters({ location: '', min_price: '', max_price: '', bhk: '' });
  };


  return (
    <PageTransition>
      {/* FIX: pt-28 ensures this content clears the fixed Navbar */}
      <div className="max-w-7xl mx-auto py-10 px-4 text-text-light pt-28"> 
        <h1 className="text-4xl font-extrabold text-brand-primary mb-6">Browse All Active Listings</h1>

        {/* --- Filters Section (FIXED LAYOUT) --- */}
        <motion.form 
            onSubmit={handleSearch}
            initial={{ opacity: 0, y: -20 }} 
            animate={{ opacity: 1, y: 0 }} 
            className="bg-bg-dark-card p-6 rounded-xl shadow-lg mb-8 border border-gray-700"
        >
          {/* Row 1: Input Fields */}
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4 items-end mb-4">
              
              <div className="col-span-1">
                  <label className="block text-sm font-medium text-text-muted mb-1">Location</label>
                  <input type="text" name="location" placeholder="Locality" value={filters.location} onChange={handleFilterChange} className="w-full p-2 rounded-lg bg-bg-dark-secondary border border-gray-600 text-text-light placeholder-text-muted" />
              </div>
              <div className="col-span-1">
                  <label className="block text-sm font-medium text-text-muted mb-1">Min Price (L)</label>
                  <input type="number" name="min_price" placeholder="Min Price" value={filters.min_price} onChange={handleFilterChange} className="w-full p-2 rounded-lg bg-bg-dark-secondary border border-gray-600 text-text-light placeholder-text-muted" />
              </div>
              <div className="col-span-1">
                  <label className="block text-sm font-medium text-text-muted mb-1">Max Price (L)</label>
                  <input type="number" name="max_price" placeholder="Max Price" value={filters.max_price} onChange={handleFilterChange} className="w-full p-2 rounded-lg bg-bg-dark-secondary border border-gray-600 text-text-light placeholder-text-muted" />
              </div>
              <div className="col-span-1">
                  <label className="block text-sm font-medium text-text-muted mb-1">BHK</label>
                  <select name="bhk" value={filters.bhk} onChange={handleFilterChange} className="w-full p-2 rounded-lg bg-bg-dark-secondary border border-gray-600 text-text-light placeholder-text-muted">
                      <option value="">Any</option>
                      <option value="1">1 BHK</option>
                      <option value="2">2 BHK</option>
                      <option value="3">3 BHK</option>
                      <option value="4">4+ BHK</option>
                  </select>
              </div>
          </div>
          
          {/* Row 2: Action Buttons (Forced onto a new row, visible) */}
          <div className="grid grid-cols-2 gap-4 w-full md:w-1/3 md:ml-auto">
            {/* SEARCH BUTTON (CRITICAL FIX) */}
            <motion.button 
                type="submit" 
                whileHover={{ scale: 1.05 }} 
                className="bg-brand-secondary text-white py-2 px-4 rounded-lg font-semibold hover:bg-orange-600 transition w-full h-10"
            >
                Search
            </motion.button>
            
            {/* Clear Button */}
            <motion.button 
                type="button" 
                whileHover={{ scale: 1.05 }} 
                onClick={handleClear} 
                className="bg-gray-600 text-white py-2 px-4 rounded-lg font-semibold hover:bg-gray-700 transition w-full h-10"
            >
                Clear Filters
            </motion.button>
          </div>

        </motion.form>

        {/* Property Listings Grid */}
        {loading ? (
          <p className="text-center text-brand-primary text-xl">Loading properties...</p>
        ) : properties.length === 0 ? (
          <p className="text-center text-text-muted text-xl">No properties found matching your criteria.</p>
        ) : (
          <motion.div layout className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
            <AnimatePresence>
              {properties.map((property) => (
                <motion.div key={property.id} layout initial={{ opacity: 0, scale: 0.8 }} animate={{ opacity: 1, scale: 1 }} exit={{ opacity: 0, scale: 0.8 }} transition={{ duration: 0.3 }}>
                  <ListingCard listing={property} />
                </motion.div>
              ))}
            </AnimatePresence>
          </motion.div>
        )}
      </div>
    </PageTransition>
  );
};

export default PropertiesPage;