import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Link } from 'react-router-dom';
import PageTransition from '../components/Common/PageTransition';
import ListingCard from '../components/Listings/ListingCard'; 
import { useAuth } from '../context/AuthContext';

// --- Animation Variants ---
const cardVariants = { hidden: { opacity: 0, y: 10 }, visible: { opacity: 1, y: 0, transition: { duration: 0.3 } } };
const statVariants = { hidden: { y: 20, opacity: 0 }, visible: { y: 0, opacity: 1 } };

// --- Simulation Data (Ensure these match the structure of your backend's data) ---
const dummyUserStats = { predictions: 5, inquiries: 3, favorites: 7 };
const dummyHistory = [
    { id: 1, location: 'Hebbal', bhk: 2, price: 72.00, area: 1200, date: '10/29/2025' },
    { id: 2, location: 'Whitefield', bhk: 3, price: 85.50, area: 1600, date: '10/29/2025' }
];
const dummyInquiries = [{ id: 1, prop: '3BHK HSR', agent: 'Agent Bob', date: 'Oct 20', status: 'Pending' }];
const dummyFavorites = [
     { id: 1, price: '98L', location: 'Sarjapur', bhk: 3, area: 1300, imageUrl: 'https://picsum.photos/seed/fav1/400/250' },
];


const UserDashboard = () => {
    const { user } = useAuth();
    const [activeTab, setActiveTab] = useState('history');
    const [loading, setLoading] = useState(true);
    
    // State initialization
    const [history, setHistory] = useState(dummyHistory);
    const [inquiries, setInquiries] = useState(dummyInquiries);
    const [favorites, setFavorites] = useState(dummyFavorites);
    const [stats] = useState(dummyUserStats); 

    useEffect(() => {
        // NOTE: In a live app, fetchPredictionHistory(), etc., would run here
        setTimeout(() => {
            setLoading(false);
        }, 800);
    }, []);


    const renderContent = () => {
        switch (activeTab) {
            case 'history': return <PredictionHistoryLayout data={history} />;
            case 'inquiries': return <MyInquiriesList data={inquiries} />;
            case 'favorites': return <SavedPropertiesGrid data={favorites} />;
            default: return null;
        }
    };

    return (
        <PageTransition>
            <div className="max-w-7xl mx-auto py-10 px-4 text-text-light pt-24">
                <h1 className="text-4xl font-extrabold text-brand-primary mb-2">Welcome Back, {user?.username || 'Buyer'} </h1>
                <p className="text-text-muted mb-8">Your personal activity summary.</p>

                {/* --- Quick Stats Cards & Loan CTA (NEW LAYOUT) --- */}
                <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-10">
                    
                    {/* Loan Calculator Call-to-Action (Unique feature for user) */}
                    <motion.div 
                        initial={{ scale: 0.9 }} 
                        animate={{ scale: 1 }} 
                        transition={{ type: 'spring', duration: 0.8 }}
                        className="col-span-1 p-6 bg-brand-secondary rounded-xl shadow-2xl flex flex-col justify-center items-center text-center"
                    >
                        <p className="text-xl font-bold text-white mb-3">Check Your Loan Power</p>
                        <Link to="/loan-calculator">
                            <motion.button 
                                whileHover={{ scale: 1.05 }} 
                                className="bg-white text-brand-secondary font-bold py-2 px-6 rounded-full transition shadow-md"
                            >
                                Start Calculator
                            </motion.button>
                        </Link>
                    </motion.div>
                    
                    {/* Existing Quick Stats Cards (Condensed) */}
                    <motion.div variants={statVariants} initial="hidden" animate="visible" transition={{ delay: 0.1 }} className="p-6 bg-bg-dark-card rounded-xl shadow-xl border-l-4 border-brand-primary">
                        <p className="text-sm font-medium text-text-muted">Predictions Made</p>
                        <h3 className="text-4xl font-extrabold text-white mt-1">{stats.predictions}</h3>
                    </motion.div>
                    <motion.div variants={statVariants} initial="hidden" animate="visible" transition={{ delay: 0.2 }} className="p-6 bg-bg-dark-card rounded-xl shadow-xl border-l-4 border-brand-secondary">
                        <p className="text-sm font-medium text-text-muted">Inquiries Sent</p>
                        <h3 className="text-4xl font-extrabold text-white mt-1">{stats.inquiries}</h3>
                    </motion.div>
                    <motion.div variants={statVariants} initial="hidden" animate="visible" transition={{ delay: 0.3 }} className="p-6 bg-bg-dark-card rounded-xl shadow-xl border-l-4 border-brand-accent">
                        <p className="text-sm font-medium text-text-muted">Favorites</p>
                        <h3 className="text-4xl font-extrabold text-white mt-1">{stats.favorites}</h3>
                    </motion.div>
                </div>
                
                {/* Animated Tabs */}
                <div className="flex space-x-6 border-b border-gray-700 mb-8">
                    {['history', 'inquiries', 'favorites'].map((tab) => (
                        <motion.button
                            key={tab}
                            onClick={() => setActiveTab(tab)}
                            whileHover={{ scale: 1.05 }}
                            className={`py-3 px-4 capitalize font-semibold relative transition ${
                                activeTab === tab ? 'text-brand-accent' : 'text-text-muted hover:text-white'
                            }`}
                        >
                            {tab.replace(/s$/, '')}
                            {activeTab === tab && (
                                <motion.div layoutId="userUnderline" className="absolute bottom-0 left-0 right-0 h-[3px] bg-brand-accent" />
                            )}
                        </motion.button>
                    ))}
                </div>

                {loading ? <p className="text-center text-brand-primary">Loading Data...</p> : (
                    <AnimatePresence mode="wait">
                        {renderContent()}
                    </AnimatePresence>
                )}
            </div>
        </PageTransition>
    );
};

export default UserDashboard;

// --- Sub-Component Implementations ---

// 3.1 Prediction History Layout (Matches Screenshot - Unique Buyer Content)
const PredictionHistoryLayout = ({ data }) => (
    <motion.div key="history" variants={cardVariants} initial="hidden" animate="visible" className="grid grid-cols-1 md:grid-cols-2 gap-8">
        {/* Left Side: Start Prediction Button */}
        <div className="bg-bg-dark-card p-6 rounded-xl shadow-lg flex flex-col justify-between">
            <div>
                <h3 className="text-2xl font-semibold text-brand-primary mb-3">Run a New Analysis</h3>
                <p className="text-lg text-text-muted mb-6">Instantly calculate the market value of any property.</p>
            </div>
            <Link to="/predict" className="w-full">
                <motion.button whileHover={{ scale: 1.05 }} className="w-full bg-brand-primary text-white py-3 px-4 rounded-lg font-semibold shadow-md transition">Start Prediction</motion.button>
            </Link>
        </div>
        
        {/* Right Side: History Table */}
        <div className="bg-bg-dark-card p-6 rounded-xl shadow-lg">
            <h3 className="text-2xl font-semibold text-brand-primary mb-4">Your Prediction History</h3>
            <div className="overflow-x-auto">
                <table className="min-w-full divide-y divide-gray-700">
                    <thead className="bg-bg-dark-secondary">
                        <tr>
                            {['Location', 'Area (sqft)', 'Predicted Price', 'Date'].map((header) => (
                                <th key={header} className="px-6 py-3 text-left text-xs font-medium text-brand-accent uppercase tracking-wider">{header}</th>
                            ))}
                        </tr>
                    </thead>
                    <motion.tbody className="divide-y divide-gray-700">
                        <AnimatePresence>
                        {data.map((item, index) => (
                            <motion.tr key={item.id} initial={{ opacity: 0, y: 10 }} animate={{ opacity: 1, y: 0 }} transition={{ delay: index * 0.05 }} className="hover:bg-gray-800/50">
                                <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">{item.location}</td>
                                <td className="px-6 py-4 whitespace-nowrap text-sm">{item.area}</td>
                                <td className="px-6 py-4 whitespace-nowrap text-md font-bold text-green-500">{item.price} L</td>
                                <td className="px-6 py-4 whitespace-nowrap text-xs text-text-muted">{item.date}</td>
                            </motion.tr>
                        ))}
                        </AnimatePresence>
                    </motion.tbody>
                </table>
            </div>
        </div>
    </motion.div>
);

// 3.2 My Inquiries List
const MyInquiriesList = ({ data }) => (
    <motion.div key="inquiries" variants={cardVariants} initial="hidden" animate="visible" className="space-y-4">
        <h3 className="text-2xl font-semibold text-brand-primary mb-4">My Sent Inquiries</h3>
        {data.map((item, index) => (
            <motion.div key={item.id} initial={{ x: -20, opacity: 0 }} animate={{ x: 0, opacity: 1 }} transition={{ delay: index * 0.05 }} className="bg-bg-dark-card p-4 rounded-lg flex justify-between items-center border-l-4 border-yellow-500 hover:bg-gray-800/50">
                <div>
                    <p className="font-bold text-lg">{item.prop}</p>
                    <p className="text-sm text-text-muted">Agent: {item.agent} | Sent: {item.date}</p>
                </div>
                <div className="flex items-center space-x-3">
                     <p className={`text-sm font-medium ${item.status === 'Replied' ? 'text-green-500' : 'text-yellow-500'}`}>{item.status}</p>
                     <motion.button whileHover={{ scale: 1.05 }} className="text-xs bg-brand-secondary px-3 py-1 rounded text-white transition">Follow Up</motion.button>
                </div>
            </motion.div>
        ))}
    </motion.div>
);

// 3.3 Saved Properties Grid (Wishlist)
const SavedPropertiesGrid = ({ data }) => (
    <motion.div key="favorites" variants={cardVariants} initial="hidden" animate="visible" className="space-y-4">
        <h3 className="text-2xl font-semibold text-brand-primary mb-4">Saved Properties (Wishlist)</h3>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            {data.map(item => (
                <ListingCard key={item.id} listing={item} onClick={() => console.log('Open Modal')}/>
            ))}
        </div>
    </motion.div>
);